import React from "react"
import { Card } from "react-bootstrap"

const SolutionCard = ({ icon, title, text }) => (
  <Card style={{ fontSize: `0.75em` }} border="white">
    <Card.Header style={{ padding: `0.25em 1.25em` }}>
      <img
        alt={title + "icon"}
        src={icon}
        className="d-inline-block"
        style={{ margin: `0.25em 0.5em` }}
      />
      {title}
    </Card.Header>
    <Card.Body>
      <Card.Text>{text}</Card.Text>
    </Card.Body>
  </Card>
)

export default SolutionCard
