import React from "react"
import { Button, Card } from "react-bootstrap"

class ProductData {
  constructor(productName, icon, link, linkText, description) {
    this.productName = productName
    this.icon = icon
    this.link = link
    this.linkText = linkText
    this.description = description
  }
}
const ProductCard = ({ productData }) => (
  <Card style={{ fontSize: `0.75em`, width: '18rem' }} border="white">
    <Card.Header style={{ padding: `0.25em 1.25em` }}>
      <img
        alt={productData.productName + " product icon"}
        src={productData.icon}
        className="product-image"
      />
    </Card.Header>
    <Card.Body>
      <Card.Title>{productData.productName}</Card.Title>
      <Card.Text>{productData.description}</Card.Text>
      <Button variant="primary" href={productData.link}>
        {productData.linkText}
      </Button>
    </Card.Body>
  </Card>
)

export { ProductCard, ProductData }

