import React from "react"
import { Button, Card } from "react-bootstrap"
import imgAmazon from "../images/amzn_logo.png"

class AmazonStoreData {
  constructor() {
    this.link = "/store"
    this.linkText = "Visit our store"
    this.description = "Find 100% handpicked tools and educational toys from our Amazon storefront"
  }
}
const storeData = new AmazonStoreData();

const AmazonStore = () => (
  <Card style={{ fontSize: `0.75em`, width: '18rem' }} border="white">
    <Card.Header style={{ padding: `0.25em 1.25em` }}>
      <img
        alt={"Visit our store"}
        src={imgAmazon}
        style={{ height: `60px`, margin: 0 }}
      />
    </Card.Header>
    <Card.Body style={{ background: `#f9f9f9` }}>
      <Card.Title>{storeData.linkText}</Card.Title>
      <Card.Text>{storeData.description}</Card.Text>
      <Button variant="primary" href={storeData.link}>
        Go
      </Button>
    </Card.Body>
  </Card>
)

export { AmazonStore }

